import React from "react";
import { HexColorPicker, RgbaColorPicker } from "react-colorful";
import { GoPencil } from "react-icons/go";
import { RiEraserFill } from "react-icons/ri";
import {
	AiOutlinePlusCircle,
	AiOutlineMinusCircle,
	AiOutlineClear,
	AiOutlineDownload,
} from "react-icons/ai";
import { IoMdReturnLeft } from "react-icons/io";
import { BsBoundingBoxCircles } from "react-icons/bs";
import { useSvgDrawing } from "react-hooks-svgdrawing";
import { clear } from "console";
import square from '.././svg/square.svg'
import s800_600 from '.././svg/800x600.svg'
import s600_800 from '.././svg/600x800.svg'
import downloadsvg from '.././svg/download.svg'
import backsvg from '.././svg/back.svg'
import removesvg from '.././svg/remove.svg'
import pencilsvg from '.././svg/pencil.svg'
import eraserSvg from '.././svg/eraser.svg'
import plussvg from '.././svg/plus.svg'
import minussvg from '.././svg/minus.svg'

const CanvasField: React.FC = () => {
	const canvasRef = React.useRef(null);
	const toolsRef = React.useRef<any>(null);
	// const [color, setColor] = React.useState("rgb(0,0,0,0.1)");
	const [color, setColor] = React.useState({ r: 200, g: 150, b: 35, a: 0.5 });
	const [lineWidth, setLineWidth] = React.useState(2);
	const [currentTool, setCurrentTool] = React.useState("pencil");
	const params = {
		height: 22,
		width: 22,
		mt: 25,
		ml: 25,
	};

	const drawLines = (canvas: any) => {
		const context = canvas.getContext("2d");
		context.beginPath();
		context.moveTo(30, 20);
		context.lineTo(100, 80);
		context.lineTo(150, 30);
		context.closePath();
		// context.closePath();
		context.strokeStyle = "red";
		context.stroke();
	};

	const drawBoxes = (canvas: any) => {
		console.log("🚀 canvas", canvas.width);
		const context = canvas.getContext("2d");

		let numbersOfBoxes = Math.trunc(
			canvas.width / (params.width + params.ml)
		);
		console.log("🚀numbersOfBoxes", numbersOfBoxes);

		for (let i = 0; i < numbersOfBoxes; i++) {
			context.fillStyle = "#ffffff";
			context.fillRect(
				i * (params.width + params.ml) + params.ml,
				params.mt,
				params.width,
				params.height
			);
		}
	};

	const [mouse, setMouse] = React.useState({ x: 0, y: 0 });
	const [drawing, setDrawing] = React.useState(false);
	const [canvas, setCanvas] = React.useState<any>();
	const [context, setContext] = React.useState<any>();
	const [renderRef, draw] = useSvgDrawing({
		penWidth: 10, // pen width
		penColor: "#e00", // pen color
		// close: false, // Use close command for path. Default is false.
		curve: true, // Use curve command for path. Default is true.
		delay: 20, // Set how many ms to draw points every.
		// fill: ''// Set fill attribute for path. default is `none`
	});

	const startDrawing = (e: any) => {
		const canvas: any = canvasRef.current;
		const context = canvas.getContext("2d");
		let newCoords = {
			x: e.nativeEvent.clientX,
			y: e.nativeEvent.clientY,
		};
		setMouse(newCoords);
		setDrawing(true);
		// context.strokeStyle('rgb(255,0,120');
		context.beginPath();
		// context.moveTo(mouse.x, mouse.y);
		context.moveTo(newCoords.x, newCoords.y);
	};

	const moveDrawing = (e: any) => {
		if (drawing == true) {
			const canvas: any = canvasRef.current;
			const context = canvas.getContext("2d");
			let newCoords = {
				x: e.nativeEvent.clientX,
				y: e.nativeEvent.clientY,
			};
			setMouse(newCoords);
			context.strokeStyle = color;
			context.lineWidth = draw?.instance?.penWidth;
			context.shadowBlur = 5;
			context.shadowColor = color;
			//    ctx.shadowOffsetX = -offset;
			// console.log(color)
			// ('rgb(255,0,120');
			// context.lineTo(mouse.x, mouse.y);
			context.lineTo(newCoords.x, newCoords.y);
			context.stroke();
		}
	};

	const endDrawing = (e: any) => {
		const canvas: any = canvasRef.current;
		const context = canvas.getContext("2d");
		setContext(context);
		let newCoords = {
			x: e.nativeEvent.clientX,
			y: e.nativeEvent.clientY,
		};
		setMouse(newCoords);
		context.lineTo(newCoords.x, newCoords.y);
		context.stroke();
		// context.strokeStyle('rgb(255,0,120');
		context.closePath();
		setDrawing(false);
		console.log("💚 canvas", canvas);
	};

	const clearAll = () => {
		draw.clear();
		// const canvas: any = canvasRef.current;
		// const context = canvas.getContext("2d");
		// context.clearRect(0, 0, 900, 600);
	};

	const save = () => {
		const canvas: any = canvasRef.current;
		const context = canvas.getContext("2d");
		// context.save();
		// draw.
		context.save(0, 0, 900, 600);

		// context.saveLayer(0,0,100,100,'mPaint1');
	};

	const restore = () => {
		draw.undo();
	};

	const setLineWidth__func = (num: number) => {
		let newNum = lineWidth + num;
		console.log("🚀 newNum", newNum);
		if (newNum > 0 || newNum < 100) {
			setLineWidth(newNum);
			// console.log("newNum", newNum);
			draw.changePenWidth(newNum);
		}
	};

	const downloadpng = () => {
		draw.download("png");
        draw.download('svg')
	};

	const getRGBA = () => {
		// let currentColor = `rgba(${color.r},${color.g},${color.b},${color.a})`
		return `rgba(${color.r},${color.g},${color.b},${color.a})`;
	};

	const tools = [
		{
			// logo: <GoPencil className="p-1" size={27} />,
            // logo: <img src={pencilsvg} width={27} className="p-1" alt="" />,
            logo: pencilsvg,
			name: "pencil",
		},
		{
            logo: eraserSvg,
			// logo: <RiEraserFill className="p-1" size={27} />,
			name: "eraser",
		},
	];

	// <button
	// 			onClick={() => clearAll()}
	// 			className="border-2 border-blue-400  text-white rounded py-1 px-4 m-1 hover:bg-blue-400/50"
	// 		>
	// 			Clear
	// 		</button>
	// 		<button
	// 			onClick={() => restore()}
	// 			className="border-2 border-blue-400  text-white rounded py-1 px-4 m-1 hover:bg-blue-400/50"
	// 		>
	// 			Restore
	// 		</button>
	// 		<button
	// 			onClick={() => downloadpng()}
	// 			className="border-2 border-blue-400  text-white rounded py-1 px-4 m-1 hover:bg-blue-400/50"
	// 		>
	// 			Download png
	// 		</button>
	const [size, setSize] = React.useState<any>({ x: '512px', y: '512px' });
	const sizes = [
		{
			// name: "512 x 512",
            img: square,
			size: { x: '512px', y: '512px' },
            // sizeClass: 'min-w-[512px] '
		},
		{
			img: s800_600,
			size: { x: '800px', y: '600px' },
			// func: () => clearAll(),
		},
		{
			img: s600_800,
			size: { x: '600px', y: '800px' },
			// func: () => clearAll(),
		},
	];
	const buttons = [
		{
			// logo: <AiOutlineClear className="p-1" size={27} />,
			func: () => clearAll(),
            logo: <img src={removesvg} width={27} alt="" />,
		},
		{
			// logo: <IoMdReturnLeft className="p-1" size={27} />,
            logo: <img src={backsvg} width={27} alt="" />,
			func: () => restore(),
		},
		{
			// logo: <AiOutlineDownload className="p-1" size={27} />,
			logo: <img src={downloadsvg} width={27} alt="" />,
			func: () => downloadpng(),
		},
	];

	React.useEffect(() => {
		//* подсвечивает выбранный инструмент
		console.log("useEffect highlight");
		let length = toolsRef.current!.childNodes.length;
		for (let i = 0; i < length; i++) {
			let el = toolsRef.current!.childNodes[i];
			if (el.attributes[0].value == currentTool) {
				el.childNodes[0].classList = "bg-blue-400 rounded p-1";
			} else {
				el.childNodes[0].classList = "p-1";
			}
		}
	}, [currentTool]);

	React.useEffect(() => {
		//* меняет параметры инструмента один раз
		// console.log("useEffect tool");
        // console.log(draw)

		switch (currentTool) {
			case "eraser":
				draw.changePenColor("rgb(255, 255, 255)");
                // draw.globalCompositeOperation()
				break;
			case "pencil":
				console.log(color);
				draw.changePenColor(getRGBA());
				draw.changePenWidth(lineWidth);
				break;
			default:
				break;
		}
	}, [currentTool]);

	let elPress;
	let pressTimer: any;

	function clearAllWindow(windowObject: any) {
		var id = Math.max(
			windowObject.setInterval(noop, 1000),
			windowObject.setTimeout(noop, 1000)
		);

		while (id--) {
			windowObject.clearTimeout(id);
			windowObject.clearInterval(id);
		}

		function noop() {}
	}

	React.useEffect(() => {
		console.log("useEffect color");
		if (currentTool == "eraser") {
			setCurrentTool("pencil");
		} else {
			draw.changePenColor(getRGBA());
		}
	}, [color]);
	return (
		<div className="pb-1">
			<div className="flex justify-between">
				<div
					ref={renderRef}
					// style={{ minWidth: size.x, minHeight: size.y }}
					style={{ minWidth: size.x, height: size.y}}
					className="bg-white cursor-crosshair"
				></div>
				{/* <canvas
					onMouseDown={(e: any) => startDrawing(e)}
					onMouseMove={(e: any) => moveDrawing(e)}
					onMouseUp={(e: any) => endDrawing(e)}
					ref={canvasRef}
					width={900}
					height={600}
					className="bg-white"
				></canvas> */}
				<div className="w-full">
					{/* <HexColorPicker */}
					<RgbaColorPicker
						// className="flex justify-self-start w-full"
						className="mt-2 ml-2"
						color={color}
						// onChange={draw.changePenColor}
						onChange={setColor}
					/>
					<div className="flex">
						<div
							ref={toolsRef}
							className="bg-white p-1 m-2 rounded shadow-md flex flex-col justify-center w-fit h-fit"
						>
							{tools.map((item: any) => (
								<div
									data-name={item.name}
									className="m-1 cursor-pointer hover:bg-blue-200 rounded flex justify-center"
									onClick={() => setCurrentTool(item.name)}
								>
									<img src={item.logo} alt="" width={30} />
                                    {/* {item.logo} */}
								</div>
							))}
						</div>

						<div className="bg-white p-1 mt-2 rounded shadow-md flex flex-col justify-center h-fit w-fit">
							{/* <AiOutlinePlusCircle */}
                                <img src={plussvg} 
                                    onMouseDown={(e: any) => {
                                        setLineWidth(prev=>{
                                            draw.changePenWidth(prev+1);
                                            return prev+1
                                        });
                                        if (e.button === 0) {
                                            // let i = 1;
                                            window.setInterval(function () {
                                                setLineWidth(prev=>{
                                                    draw.changePenWidth(prev+1);
                                                    return prev+1
                                                })
                                                // i++;
                                            }, 60);
                                        }
                                    }}
                                    onMouseUp={(e) => {
                                        clearAllWindow(window);
                                    }}
                                    onMouseLeave={(e) => clearAllWindow(window)}
                                    className="m-1 p-1 cursor-pointer hover:bg-blue-200 rounded"
                                    width={29}
                                alt="" />
							{/* /> */}
							<p className="my-[1px] mx-auto select-none">
								{lineWidth}
							</p>
                            <img src={minussvg} 
								onMouseDown={(e: any) => {
									if (lineWidth > 1) {
                                        console.log("🚀 lineWidth", lineWidth)
                                        // setLineWidth__func(-1);
                                        setLineWidth(prev=>{
                                            draw.changePenWidth(prev-1);
                                            return prev-1
                                        })
										if (e.button === 0) {
                                            // let i = -1;
											window.setInterval(function () {
                                                setLineWidth(prev=>{
                                                    if(prev > 1){
                                                        draw.changePenWidth(prev-1);
                                                        return prev-1
                                                    }else{
                                                        return prev
                                                    }
                                                })
												// setLineWidth__func(i);
                                                // console.log(i);
                                                
												// i--;
											}, 60);
										}
									}
                                    else{
                                        clearAllWindow(window);

                                    }
								}}
								onMouseUp={(e) => {
									clearAllWindow(window);
								}}
								className="m-1 p-1 cursor-pointer hover:bg-blue-200 rounded"
								width={29}
                            alt="" />
							
							
						</div>
					</div>
				</div>
			</div>

			<div className="flex">
				<div className="bg-white p-1 m-2 mr-0 mb-1 rounded shadow-md flex flex-row justify-center w-fit h-fit">
					{buttons.map((item: any) => (
						<div
							className="m-1 cursor-pointer hover:bg-blue-200 rounded flex justify-center"
							onClick={item.func}
						>
							{item.logo}
						</div>
					))}
				</div>

				<div className="bg-white p-1 m-2 mb-1 rounded shadow-md flex flex-row justify-center w-fit h-fit">
					{sizes.map((item: any) => (
						<div
							className="m-1 cursor-pointer hover:bg-blue-200 rounded flex justify-center"
							onClick={()=>setSize(item.size)}
						>
							{/* {item.name} */}
                            <img src={item.img} width='27' alt="" />
						</div>
					))}
				</div>
			</div>

			{/* <button
				onClick={() => clearAll()}
				className="border-2 border-blue-400  text-white rounded py-1 px-4 m-1 hover:bg-blue-400/50"
			>
				Clear
			</button>
			<button
				onClick={() => restore()}
				className="border-2 border-blue-400  text-white rounded py-1 px-4 m-1 hover:bg-blue-400/50"
			>
				Restore
			</button>
			<button
				onClick={() => downloadpng()}
				className="border-2 border-blue-400  text-white rounded py-1 px-4 m-1 hover:bg-blue-400/50"
			>
				Download png
			</button> */}
		</div>
	);
};

export default CanvasField;
